  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image {
  background-image: url("imac.202c52ff.jpeg");
  background-size: 160px 134px;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__name {
  color: #060b35;
  width: 100%;
  margin-bottom: 4px;
  font-family: inherit;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  width: 100%;
  margin-bottom: 16px;
  font-family: inherit;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 16px;
  margin-bottom: 24px;
  display: flex;
}

.card__price {
  justify-content: space-between;
